document.addEventListener('DOMContentLoaded', function () {
    const jsFilterGroup = document.querySelectorAll('.jsFilterGroup');


    for (let index = 0; index < jsFilterGroup.length; index++) {
        let item = jsFilterGroup[index];
        let trigger = item.querySelector('.jsFilterGroupTrigger');
        let body = item.querySelector('.jsFilterGroupBody');
        let content = item.querySelector('.jsFilterGroupContent');


        trigger.addEventListener('click', () => {
            item.classList.toggle('is-active');
            checkClass()
        })

        if (window.innerWidth > 991){
            checkClass()
        } else{
            if(item.classList.contains('is-active')){
                item.classList.remove('is-active')
            }
        }
        
        function checkClass(){
            if(item.classList.contains('is-active')){
                body.style.maxHeight = content.offsetHeight + 'px';
            } else {
                body.style.maxHeight = '';
            }
        }
    }




    if(document.querySelector('.jsFilter')){
        const jsFilter = document.querySelector('.jsFilter');

        const jsFilterTrigger = jsFilter.querySelector('.jsFilterTrigger');
        const jsFilterContent = jsFilter.querySelector('.jsFilterContent');
        const jsFilterClose = jsFilter.querySelector('.jsFilterClose');
        
        const jsFilterClear = jsFilter.querySelectorAll('.jsFilterClear');
        const jsFilterCount = jsFilter.querySelectorAll('.jsFilterCount');

        const jsCheckbox = jsFilter.querySelectorAll('.jsCheckbox');
        const jsRadio = jsFilter.querySelectorAll('.jsRadio');
        let checkeds = 0;
        
        function checkInputs(){
            let ch = jsFilter.querySelectorAll('.jsCheckbox:checked').length;
            let rad = jsFilter.querySelectorAll('.jsRadio:checked').length;
            checkeds += ch + rad;
        }

        checkInputs()

        // console.log(checkeds);

        for (let index = 0; index < jsCheckbox.length; index++){
            let item = jsCheckbox[index];

            item.addEventListener('change', function(){
                if(item.checked){
                    checkeds++
                } else{
                    checkeds--
                }
                // console.log(checkeds);
            })
        }

        for (let index = 0; index < jsRadio.length; index++){
            let item = jsRadio[index];

            item.addEventListener('change', function(){
                if(item.checked){
                    checkeds++
                } else{
                    checkeds--
                }
                // console.log(checkeds);
            })
        }




        document.addEventListener('click', e => {
            const target = e.target 
            // console.log('click');
            if (jsFilter.classList.contains('is-active')) {
                // console.log('submit');
                if (!target.closest('.jsFilter')) {
                    filterClose();
                    window.backdropClose()
                    // console.log('close');
                } else{
                    // console.log('not close');
                }
            }
        })

        jsFilterTrigger.addEventListener('click', () => {
            jsFilter.classList.add('is-active');
            jsFilterContent.classList.add('is-active');
            window.backdropOpen()
        })

        jsFilterClose.addEventListener('click', () => {
            filterClose()
            window.backdropClose()
        })

        
        function filterClose(){
            jsFilter.classList.remove('is-active')
            jsFilterContent.classList.remove('is-active')
        }
    }
})

